*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.App{color:#000}.App-logo{height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.input{width:100%;background-color:#fff;position:-webkit-sticky;position:sticky;top:0;z-index:2}.initialInput-table{width:100%}


.inputRowTitle{font-size:1.2em;text-align:center;margin-bottom:20px}@media only screen and (max-width: 910px){.inputRowTitle{font-size:1em;margin-bottom:5px}}

.inputRow{width:100%;height:40px}.inputRow-label{text-align:left;font-size:1em}.inputRow-input{text-align:left;padding:0 0 0 10px}.inputRow-input input{width:100%;text-align:center;border:none;border-radius:2px;outline:none;line-height:1.75}.inputRow-units{font-size:.6em}@media only screen and (max-width: 910px){.inputRow-label{font-size:.8em}.inputRow{height:20px}}

.inputTable{margin:auto}


.inputColumn{flex:2 1;border-radius:20px;padding:10px;border:lightsteelblue 1px solid;background:lightsteelblue;box-shadow:10px 10px 5px 0px rgba(0,0,0,0.75);margin:0 20px}@media only screen and (max-width: 910px){.inputColumn{margin:10px 20px}}

.weekLabel{background:#fff;height:50px;position:relative;z-index:2;display:block}.weekLabel-wrapper{display:flex;padding:10px 10%;text-align:center}.weekLabel-dayLabel{flex:1 1}.weekLabel-label{flex:1 1;font-weight:600}@media only screen and (max-width: 910px){.weekLabel{font-size:.8em}.weekLabel-wrapper{padding:10px 0}.noMobile{display:none}}

.inputColumnWrapper{padding:5vh 5vw;display:flex;flex-wrap:wrap}@media only screen and (max-width: 910px){.inputColumnWrapper{padding:0 5vw 2vh;flex-direction:column}}

.result{background-color:#fff;text-align:center}

.weekRow{background:#fff;border-top:1px solid black}.weekRow-entry{flex:1 1;font-weight:600}.weekRow-entry input{width:100%;border:none;outline-color:black;background:#b0c4de;font-size:1em;margin:0;padding:4px;font-size:20px;text-align:center;font-weight:700;line-height:1.5}@media only screen and (max-width: 910px){.weekRow{font-size:.8em}.weekRow-wrapper{padding:0}.noMobile{display:none}}

.weekRow{background:#fff;border-top:1px solid black}.weekRow-wrapper{display:flex;padding:0 10%;text-align:center;align-items:center}@media only screen and (max-width: 910px){.weekRow{font-size:.8em}.weekRow-wrapper{padding:0}}

.LabelCell{flex:1 1;font-weight:600;width:100%;border:none;outline-color:none;background:#fff;font-size:1em;margin:0;padding:4px;text-align:center;font-weight:700;line-height:1.5}@media (max-width: 910px){.mobileViewHide{display:none}}

.weekRow-entry{flex:1 1;font-weight:600}.weekRow-entry input{width:100%;border:none;outline-color:black;background:#b0c4de;font-size:1em;margin:0;padding:4px;font-size:20px;text-align:center;font-weight:700;line-height:1.5}@media (max-width: 910px){.weekRow-entry input{font-size:.8em}}


.addWeekBtn{font-size:1.2em;border:none;padding:10px;border-radius:10px;margin:10px auto}

